import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Notifications from '@kyvg/vue3-notification'
import VueApexCharts from "vue3-apexcharts";

axios.defaults.timeout = 1000 * 300
axios.defaults.baseURL = 'http://localhost:86/api/'

const app = createApp(App)

app.use(VueApexCharts);

app.use(Notifications).use(VueAxios, axios)
app.use(store).use(router)

app.config.globalProperties.$axios = axios

const isPublic = window.location.pathname.toString().includes('user')

axios.defaults.headers.common.Authorization = `Bearer ${ isPublic ? store?.state?.user?.token : store?.state?.admin?.token}`
app.mount('#app')
