<template>
  <div class="right-sidebar" :class="{'right-toggled': toggled}" @click="toggled = !toggled">
    <div class="switcher-icon">
      <i class="zmdi zmdi-settings zmdi-hc-spin"></i>
    </div>
    <div class="right-sidebar-content">

      <p class="mb-0">Gaussion Texture</p>
      <hr>

      <ul class="switcher">
        <li id="theme1" @click="activateTheme('theme1')"></li>
        <li id="theme2" @click="activateTheme('theme2')"></li>
        <li id="theme3" @click="activateTheme('theme3')"></li>
        <li id="theme4" @click="activateTheme('theme4')"></li>
        <li id="theme5" @click="activateTheme('theme5')"></li>
        <li id="theme6" @click="activateTheme('theme6')"></li>
      </ul>

      <p class="mb-0">Gradient Background</p>
      <hr>

      <ul class="switcher">
        <li id="theme7" @click="activateTheme('theme7')"></li>
        <li id="theme8" @click="activateTheme('theme8')"></li>
        <li id="theme9" @click="activateTheme('theme9')"></li>
        <li id="theme10" @click="activateTheme('theme10')"></li>
        <li id="theme11" @click="activateTheme('theme11')"></li>
        <li id="theme12" @click="activateTheme('theme12')"></li>
        <li id="theme13" @click="activateTheme('theme13')"></li>
        <li id="theme14" @click="activateTheme('theme14')"></li>
        <li id="theme15" @click="activateTheme('theme15')"></li>
      </ul>

    </div>
  </div>
</template>

<script>
export default {
  name: "ColorSwitcher",
  data() {
    return {
      toggled: false
    }
  },
  methods: {
    activateTheme(name) {
      this.$store.commit('THEME', name)
      document.getElementsByTagName('body')[0].className = ` bg-${name} bg-theme pace-done`
      this.$notify({text: 'Theme add successfully', type: 'info'})
    }
  }
}
</script>

<style scoped>
li {
  cursor: pointer;
}
</style>
